<template>
  <div id="app" ref="app">
    <div id="nav" v-if="$route.meta.showNavBar">
      <li><router-link to="/"></router-link></li>
      <li><router-link to="/hot"></router-link></li>
      <li><router-link to="/search"></router-link></li>
    </div>

    <section class="routes">
      <transition
        name="custom-classes-transition"
        enter-active-class="animate__animated animate__slideInRight"
        leave-active-class="animate__animated animate__slideOutLeft"
      >
        <router-view
          @change-current-song="changeCurrentSong"
          @change-current-play-list="changeCurrentPlayList"
          :currentSongId="currentSong ? currentSong.id : null"
          :playing="playing"
          :currentPlayList="currentPlayList"
          style="
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            overflow-y: auto;
          "
        />
      </transition>
    </section>
    <audio
      ref="audio"
      :src="currentSongUrl"
      autoplay
      @playing="playing = true"
      @pause="playing = false"
      @timeupdate="timeupdate"
      @durationchange="durationchange"
    ></audio>

    <Play
      :llv="llv"
      v-if="currentSong"
      :currentSong="currentSong"
      :playing="playing"
      @toggle-playing-state="togglePlayingState"
      :currentTime="currentTime"
      :duration="duration"
      :currentPlayList="currentPlayList"
      @change-current-song="changeCurrentSong"
      @next-song="nextSong"
      @prev-song="prevSong"
    ></Play>
  </div>
</template>

<script>
import Play from "./components/Play.vue";
export default {
  components: {
    Play,
  },
  data: function() {
    return {
      currentSong: null,
      currentPlayList: [],
      playing: false,
      currentTime: 0,
      duration: 0,
      llv: 0,
    };
  },
  computed: {
    currentSongUrl: function() {
      if (this.currentSong) {
        return `https://music.163.com/song/media/outer/url?id=${this.currentSong.id}.mp3`;
      } else {
        return null;
      }
    },
  },
  methods: {
    changeCurrentSong: function(song) {
      this.currentSong = song;
      this.$refs.app.style.paddingBottom = 40 + "px";
    },
    changeCurrentPlayList: function(list) {
      this.currentPlayList = list;
    },
    togglePlayingState: function() {
      if (this.playing) {
        // 
        this.$refs.audio.pause();
      } else {
        // 
        this.$refs.audio.play();
      }
    },
    timeupdate: function(event) {
      this.currentTime = event.target.currentTime;

      var v = (this.$refs.audio.currentTime / this.$refs.audio.duration) * 100;

      this.llv = v;
    },
    durationchange: function(event) {
      this.duration = event.target.duration;
    },
    nextSong: function() {
      var index = this.currentPlayList.findIndex((item) => {
        return item.id === this.currentSong.id;
      });
      index++;
      index = index <= 0 ? this.currentPlayList.length - 1 : index;
      index = index >= this.currentPlayList.length - 1 ? 0 : index;
      console.log(index);
      this.changeCurrentSong(this.currentPlayList[index]);
    },
    prevSong: function() {
      var index = this.currentPlayList.findIndex((item) => {
        return item.id === this.currentSong.id;
      });
      index--;
      index = index >= this.currentPlayList.length - 1 ? 0 : index;
      index = index <= 0 ? this.currentPlayList.length - 1 : index;
      // console.log(index);
      this.changeCurrentSong(this.currentPlayList[index]);
    },
  },
};
</script>
